.top-bar {
  display: flex;
  justify-content: space-between;
  grid-column: 1 / -1;

  padding-block: 20px;

  @include onSmallDesktop {
    padding-block: 48px;
  }

  &__logo {
    height: 28px;
    @include hover(transform, scale(1.2));

    &-wrapper {
      display: flex;
      gap: 32px;
      align-items: center;

      @include onSmallDesktop {
        gap: 136px;
      }

      @include onDesktop {
        gap: 251px;
      }
    }
  }
}



