.nav {
display: none;

@include onSmallDesktop {
  display: block;
}

  &__list {
    display: flex;
    gap: 48px;
    list-style-type: none;
    padding: 0;
    margin: 0;
  }

  &__item {
    @include hover(transform, scale(1.2));

    &--footer {
      @include hover(transform, scale(1.05));

      @include onTablet {
        @include hover(transform, scale(1.2));
      }
    }
  }

  &__link {
    display: block;

    color: $main-text-color-white;
    text-decoration: none;
    text-transform: uppercase;
  }
}

.nav-footer__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 24px;

  @include onTablet {
    flex-direction: row;
    gap: 40px;
  }

  @include onSmallDesktop {
    gap: 64px;
  }
}
